
const _ = require('lodash');

export function gamesByOwnerFunc (games) {
  //get games by owner
  const gamesByOwners = [];
  games.forEach((game) => {
    const teamA = game?.team_a[0];
    const teamB = game?.team_b[0];
    let teamObject = [];
    if (gamesByOwners.find(obj => obj.id === teamA?.ownerId)) {
        teamObject = gamesByOwners.find(obj => obj.id === teamA?.ownerId);
        teamObject.games.push(game);
    } else {
      gamesByOwners.push({
        id: teamA?.ownerId,
        name: teamA?.owner,
        games: [game]
      })
    }
    if (gamesByOwners.find(obj => obj.id === teamB?.ownerId)) {
        teamObject = gamesByOwners.find(obj => obj.id === teamB?.ownerId);
        teamObject.games.push(game);
    } else {
      gamesByOwners.push({
        id: teamB?.ownerId,
        name: teamB?.owner,
        games: [game]
      })
    }
  });

  return gamesByOwners;
}

export function scoresOwners(games) {
  const scoresOwners = [];
  games.forEach((game) => {
    const teamA = game?.team_a[0];
    const teamB = game?.team_b[0];
    scoresOwners.push({
      name: teamA.name,
      score: teamA.score,
      id: teamA.ownerId,
      week: game.week,
      year: game.year
    });
    scoresOwners.push({
      name: teamB.name,
      score: teamB.score,
      id: teamB.ownerId,
      week: game.week,
      year: game.year
    });
  });

  return scoresOwners;

}

